.navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-black);
  padding: 1rem 2rem;
}

.navbar__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navbar__logo img {
  width: 150px;
}

.navbar__links {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.navbar__links li {
  margin: 0 1rem;
  cursor: pointer;
}

.navbar__links li:hover {
  color: var(--color-grey);
}

.navbar__login {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar__login a {
  margin: 0 1rem;
  text-decoration: none;
  transition: border 0.5s ease;
}

.navbar__login a:hover {
  border-bottom: 1px solid var(--color-golden);
}

.navbar__login div {
  width: 1px;
  height: 30px;
  background: var(--color-grey);
}

.navbar__smallscreen {
  display: none;
}

.navber__smallscreens_overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: var(--color-black);
  transition: 3s ease all;
  z-index: 3;
}
.navber__smallscreens_overlay .overlay-colse {
  color: var(--color-golden);
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (max-width: 2000px) {
  .navbar__logo img{
    width: 210px;
  }
}

@media screen and (max-width: 1150px) {
  .navbar__links {
    display: none;
  }

  .navbar__smallscreen {
    display: flex;
  }

  .navbar__links-small--screen {
    list-style: none;
  }

  .navbar__links-small--screen li {
    margin: 2rem auto;
    text-align: center;
    cursor: pointer;
    color: var(--color-golden);
    font-family: var(--font-base);
    font-size: 2rem;
  }

  .navbar__links-small--screen li:hover {
    color: var(--color-white);
  }
}

@media screen and (max-width:650px){
  .navbar{
    padding: 1rem ;
  }
  .navbar__login a{
    margin: 0.2rem;
  }

  .navbar__logo img{
    width: 120px;
  }
}
