.menu {
  flex-direction: column;
  background: var(--color-black);
}

.menu__title {
  text-align: center;
  margin-bottom: 2rem;
}

.menu__menu {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin: 2rem 0;
  justify-content: center;
  align-items: flex-start;
}

.menu__menu_heading {
  font-family: var(--font-base);
  font-weight: 600;
  font-size: 45px;
  line-height: 60px;
  color: var(--color-golden);
}

.menu__menu_wine,
.menu__menu_cocktails {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu__menu_img {
  width: 400px;
  margin: 0 2rem;
}

.menu__menu_img img {
  width: 100%;
  height: auto;
}

.menu__menu_items {
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
  width: 100%;
}

@media screen and (max-width: 2000px) {
  .menu__menu_img {
    width: 650px;
  }
}
@media screen and (max-width: 1150px) {
  .menu__menu {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .menu__menu_img {
    margin: 3rem 0;
  }
}
@media screen and (max-width: 650px) {
  .menu__menu_img {
    width: 100%;
  }
  .menu__menu_heading {
    font-size: 35px;
    line-height: 45px;
  }
}
