.laurels__awards {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top:3rem ;
  flex-wrap: wrap;
}

.laurels__award_card{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    min-width: 230px;
    margin: 1rem;
}

.laurels__award_card img{
    width: 50px;
    height: 50px;
}

.laurels__award_card__content{
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}

@media screen and (min-width:1900px) {
    .laurels__award_card{
        min-width: 390px;
    }
}

@media screen and (max-width:450px) {
    .laurels__award_card{
        min-width: 100%;
        margin: 1rem 0;
    }
}
