.aboutus {
  position: relative;
}

.aboutus__overlay {
  position: absolute;
  inset: 0;
}

.aboutus__overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

.aboutus__content {
  width: 100%;
  z-index: 2;
}

.aboutus__content_aboutus {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: right;
}

.aboutus__content_knife {
  margin: 2rem 4rem;
}

.aboutus__content_knife img {
  height: 910px;
}

.aboutus__content_history {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
}

.aboutus__content_aboutus p,
.aboutus__content_history p {
  margin: 2rem 0;
  color: var(--color-gray);
}

@media screen and (max-width: 2000px) {
  .aboutus__content_knife img {
    height: 1110px;
  }
  .aboutus__content_aboutus p,
  .aboutus__content_history p {
    margin: 4rem 0;
  }
}
@media screen and (max-width: 900px) {
  .aboutus__content {
    flex-direction: column;
  }
  .aboutus__content_knife {
    margin: 4rem 0;
  }
}
@media screen and (max-width: 650px) {
  .aboutus__overlay img {
    width: 60%;
    height: auto;
  }

  .aboutus__content_knife img {
  height:700px;
}
}
