.menuitem {
  width: 100%;
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
}
.menuitem__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menuitem__name {
  flex: 1;
}

.menuitem__sub {
  width: 100%;
  margin-top: 0.2rem;
}

.menuitem__dash {
  width: 90px;
  height: 1px;
  background: var(--color-golden);
  margin: 0 1rem;
}

.menuitem__price {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}


