.video {
  height: 100vh;
  position: relative;
}

.video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video__overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.30);
}

.video__overlay_circle {
  width: 100px;
  height: 100px;
  border: 1px solid var(--color-golden);
  border-radius: 50%;
  cursor: pointer;
}
