.gallery {
  display: flex;
  flex-direction: row;
  background: var(--color-black);
  padding: 4rem 0 4rem 6rem;
  overflow: hidden;
}

.gallery__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 500px;
  padding-right: 2rem;
}

.gallery__content button {
  margin-top: 1rem;
}

.gallery__images {
  flex: 1;
  display: flex;
  flex-direction: row;
  max-width: 50%;
  position: relative;
}

.gallery__images_container {
  display: flex;
  flex-direction: row;
  width: max-content;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.gallery__images_container::-webkit-scrollbar {
  display: none;
}

.gallery__images_card {
  position: relative;
  min-width: 301px;
  height: 447px;
  margin-right: 2rem;
}

.gallery__images_instaicon {
  position: absolute;
  color: #fff;
  font-size: 2rem;
  opacity: 0.5;
  transition: 0.5s ease;
  cursor: pointer;
}

.gallery__images_card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: 1s ease;
}

.gallery__images_card:hover {
  opacity: 0.55;
}

.gallery__images_card:hover .gallery__images_instaicon {
  opacity: 1;
}

.gallery__images_arrows {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  position: absolute;
  bottom: 5%;
}

.gallery__images_arrowicon {
  color: var(--color-golden);
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--color-black);
  border-radius: 5px;
}

.gallery__images_arrowicon:hover {
  color: var(--color-white);
}


@media screen and (min-width: 2000px) {
    .gallery__content button {
        margin-top: 2rem;
    }

    .gallery__content {
        min-width: 1000px;
        padding-right: 4rem;
    }

    .gallery__images_card {
        min-width: 400px;
        height: 547px;
    }
}

@media screen and (max-width: 1150px) {
    .gallery {
        flex-direction: column;
    }

    .gallery__images {
        max-width: 100%;
        margin: 5rem 0;
    }
}

@media screen and (max-width: 850px) {
    .gallery {
        padding: 4rem 0 4rem 4rem;
    }
}

@media screen and (max-width: 650px) {
    .gallery {
        padding: 4rem 0 4rem 2rem;
    }  
    
    .gallery__content{
        min-width: 100%;
    }

    .gallery__images_card {
        min-width: 240px;
        height: 320px;
    }
}
